.keyboard {
  display: flex;
  flex-direction: column;
  margin: 0.5rem;
  align-items: center;
  position: fixed;
  bottom: 0;
}
.keyboard div {
  display: flex;
  margin: 0;
}
.keyboard div div {
  background-color: #faf5d1;
  width: 1rem;
  height: 3rem;
  padding-inline: 0.64rem;
  text-align: center;
  margin: 0.5px;
  text-transform: uppercase;
  display: grid;
  place-content: center;
  border-radius: 5px;
  &:hover {
    background-color: #e2ddb8;
  }
  p {
    pointer-events: none;
  }
}
.keyboard .line2 div {
  padding-inline: 0.7rem;
}
.keyboard .line3 div {
  padding-inline: 0.7rem;
}
.keyboard .key-action {
  padding-inline: 0.7rem;
  width: 1.5rem;
  font-size: 0.4rem;
}
