.keyboard {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 0.5rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: fixed;
  bottom: 0;
}

.keyboard div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0;
}

.keyboard div div {
  background-color: #faf5d1;
  width: 1rem;
  height: 3rem;
  padding-inline: 0.64rem;
  text-align: center;
  margin: 0.5px;
  text-transform: uppercase;
  display: -ms-grid;
  display: grid;
  place-content: center;
  border-radius: 5px;
}

.keyboard div div:hover {
  background-color: #e2ddb8;
}

.keyboard div div p {
  pointer-events: none;
}

.keyboard .line2 div {
  padding-inline: 0.7rem;
}

.keyboard .line3 div {
  padding-inline: 0.7rem;
}

.keyboard .key-action {
  padding-inline: 0.7rem;
  width: 1.5rem;
  font-size: 0.4rem;
}
