.stats-container {
  background-color: #faf5d1;
  border-radius: 5px;
  padding: 0.5rem;
  text-align: center;
}

.stats-container p {
  font-weight: 600;
  font-size: 1.3rem;
  margin-block: 0.5rem;
}

.stats-container ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  width: 14.5rem;
  padding: 0%;
  margin: 0;
  list-style-type: none;
}

.stats-container ul li {
  margin: 0.5rem;
  font-size: 1.1rem;
  font-weight: 500;
  letter-spacing: 1px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  text-align: center;
}

.stats-container ul li span {
  padding-inline: 0.5rem;
  padding-block: 0.3rem;
  border-radius: 5px;
  font-size: 1rem;
  background-color: #182e32;
  color: white;
}

.stats-container .the-word {
  font-weight: 600;
}

.stats-container .lost-word {
  font-size: 1rem;
  font-weight: 400;
  margin-block: 0.4rem;
}
