.stats-container {
  background-color: #faf5d1;
  border-radius: 5px;
  padding: 0.5rem;
  text-align: center;
  p {
    font-weight: 600;
    font-size: 1.3rem;
    margin-block: 0.5rem;
  }
  ul {
    display: flex;
    justify-content: space-evenly;
    width: 14.5rem;
    padding: 0%;
    margin: 0;
    list-style-type: none;
    li {
      margin: 0.5rem;
      font-size: 1.1rem;
      font-weight: 500;
      letter-spacing: 1px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-content: center;
      text-align: center;
      span {
        padding-inline: 0.5rem;
        padding-block: 0.3rem;
        border-radius: 5px;
        font-size: 1rem;
        background-color: #182e32;
        color: white;
      }
    }
  }
  .the-word {
    font-weight: 600;
  }
  .lost-word {
    font-size: 1rem;
    font-weight: 400;
    margin-block: 0.4rem;
  }
}
