.caps-container {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.caps-box {
  width: 20rem;
  height: 20rem;
  margin: auto;
  border-radius: 1rem;
  border: 1px white solid;
  pointer-events: none;
  h3 {
    color: white;
    font-size: 2.5rem;
    font-weight: 600;
    margin: 0.2em;
  }
  p {
    color: white;
    font-size: 0.9rem;
    width: 25ch;
  }
}
