:root {
  --clr-bg: #182e32;
  --clr-primary: #faf5d1;
}

html {
  font-family: "Montserrat", sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: var(--clr-bg);
}

h1 {
  color: white;
  margin-top: 0;
}

.app-container {
  text-align: center;
  padding: 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.game-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding: 0.5em;
  border-radius: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.game-container .board-line {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 1px;
  margin-inline: auto;
}

.game-container .board-tile {
  width: 3rem;
  height: 3rem;
  border-radius: 5px;
  margin-bottom: 1px;
  background-color: var(--clr-primary);
  text-transform: uppercase;
  display: -ms-grid;
  display: grid;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 2rem;
}

.game-container .correct {
  background-color: #beecbb;
  -webkit-animation: correct 0.2s ease;
          animation: correct 0.2s ease;
}

@-webkit-keyframes correct {
  0% {
    background-color: #beecbb;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    background-color: #67be61;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    background-color: #beecbb;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes correct {
  0% {
    background-color: #beecbb;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    background-color: #67be61;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    background-color: #beecbb;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes close {
  0% {
    background-color: #efcebb;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    background-color: #dda280;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    background-color: #efcebb;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes close {
  0% {
    background-color: #efcebb;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    background-color: #dda280;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    background-color: #efcebb;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.game-container .close {
  background-color: #efcebb;
  -webkit-animation: close 0.2s ease;
          animation: close 0.2s ease;
}

.game-container .incorrect {
  background-color: #e4e2dc;
}

.game-over-btn {
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  margin-top: 3rem;
  color: var(--clr-bg);
  text-transform: uppercase;
  font-weight: 600;
  background-color: #efcebb;
  border: 1px solid var(--clr-primary);
  border-radius: 5px;
  -webkit-transition: -webkit-transform 0.1s ease;
  transition: -webkit-transform 0.1s ease;
  transition: transform 0.1s ease;
  transition: transform 0.1s ease, -webkit-transform 0.1s ease;
}

.game-over-btn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-box-shadow: 0 0 3px var(--clr-primary);
          box-shadow: 0 0 3px var(--clr-primary);
}
