:root {
  --clr-bg: #182e32;
  --clr-primary: #faf5d1;
}

html {
  font-family: "Montserrat", sans-serif;
  box-sizing: border-box;
  background-color: var(--clr-bg);
}
h1 {
  color: white;
  margin-top: 0;
}
.app-container {
  text-align: center;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.game-container {
  display: flex;
  flex-direction: column;
  padding: 0.5em;
  border-radius: 5px;
  width: fit-content;
  .board-line {
    display: flex;
    gap: 1px;
    margin-inline: auto;
  }
  .board-tile {
    width: 3rem;
    height: 3rem;
    border-radius: 5px;
    margin-bottom: 1px;
    background-color: var(--clr-primary);
    text-transform: uppercase;
    display: grid;
    align-items: center;
    font-size: 2rem;
  }
  .correct {
    background-color: #beecbb;
    animation: correct 0.2s ease;
  }
  @keyframes correct {
    0% {
      background-color: #beecbb;
      transform: scale(1);
    }
    50% {
      background-color: #67be61;
      transform: scale(1.2);
    }
    100% {
      background-color: #beecbb;
      transform: scale(1);
    }
  }
  @keyframes close {
    0% {
      background-color: #efcebb;
      transform: scale(1);
    }
    50% {
      background-color: #dda280;
      transform: scale(1.2);
    }
    100% {
      background-color: #efcebb;
      transform: scale(1);
    }
  }
  .close {
    background-color: #efcebb;
    animation: close 0.2s ease;
  }
  .incorrect {
    background-color: #e4e2dc;
  }
}
.game-over-btn {
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  padding: 0.5rem 1rem;
  margin-top: 3rem;
  color: var(--clr-bg);
  text-transform: uppercase;
  font-weight: 600;
  background-color: #efcebb;
  border: 1px solid var(--clr-primary);
  border-radius: 5px;
  transition: transform 0.1s ease;
  &:hover {
    transform: scale(1.1);
    box-shadow: 0 0 3px var(--clr-primary);
  }
}
