.caps-container {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  inset: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.caps-box {
  width: 20rem;
  height: 20rem;
  margin: auto;
  border-radius: 1rem;
  border: 1px white solid;
  pointer-events: none;
}

.caps-box h3 {
  color: white;
  font-size: 2.5rem;
  font-weight: 600;
  margin: 0.2em;
}

.caps-box p {
  color: white;
  font-size: 0.9rem;
  width: 25ch;
}
