.error-word-container {
  position: fixed;
  background-color: #faf5d1;
  border: 1px #182e32 solid;
  border-radius: 5px;
  padding: 1rem 2rem;
  color: red;
  opacity: 0;
  -webkit-animation: opacity 3s ease-in-out;
          animation: opacity 3s ease-in-out;
}

@-webkit-keyframes opacity {
  0% {
    opacity: 0%;
  }
  5% {
    opacity: 100%;
  }
  80% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}

@keyframes opacity {
  0% {
    opacity: 0%;
  }
  5% {
    opacity: 100%;
  }
  80% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}
